/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { useSelector, useDispatch } from "react-redux";
import {
  pageChange,
  loadDetfault,
} from "../../stateManagement/actions/dataActions";
export const Pagenation = ({ newPageNumber }) => {
  const { searchCache, total } = useSelector((state) => state.appSetting);
  const dispatch = useDispatch();

  const { list } = useSelector((state) => state.data);
  const { page, limit } = searchCache;

  const pagelimit = total / limit;

  const nextPage = () => {
    let _searchCache = searchCache;
    _searchCache.page += 1;
    dispatch(pageChange(_searchCache));
  };
  const previousPage = () => {
    let _searchCache = searchCache;
    _searchCache.page -= 1;
    dispatch(pageChange(_searchCache));
  };
  const getPages = () => {
    return Array.apply(null, { length: pagelimit }).map((v, i) => (
      <li
        key={i}
        onClick={() => {
          if (i + 1 !== page) {
            let _searchCache = searchCache;
            _searchCache.page = i + 1;
            dispatch(pageChange(_searchCache));
          }
        }}
        className={i + 1 === page ? "active" : ""}
      >
        <a>{i + 1}</a>
      </li>
    ));
  };
  const getPagination = () => {
    if (searchCache.title == "") {
      if (list.length === 0) {
        return;
      }
      return (
        <ul className="pagination" style={{ textAlign: "center" }}>
          <li className={page === 1 ? "disabled waves-effect" : "waves-effect"}>
            <a
              onClick={() => {
                if (!(page - 1 <= 0)) {
                  previousPage();
                }
              }}
            >
              <i className="material-icons">chevron_left</i>
            </a>
          </li>
          {getPages()}
          <li
            className={
              page === pagelimit ? "disabled waves-effect" : "waves-effect"
            }
          >
            <a
              onClick={() => {
                if (page !== pagelimit) {
                  nextPage();
                }
              }}
            >
              <i className="material-icons">chevron_right</i>
            </a>
          </li>
        </ul>
      );
    } else {
      return (
        <>
          <div className="search-page">
            <div className="end-result waves-effect">
              No More Result <i className="material-icons">&#xe612;</i>{" "}
            </div>
            <div
              className="exit-search waves-effect"
              onClick={() => dispatch(loadDetfault())}
            >
              Exit Search <i className="material-icons">&#xe879;</i>
            </div>
          </div>
        </>
      );
    }
  };
  return <div className="pagenation">{getPagination()}</div>;
};
